.sponsors {
  background: var(--yellow-color);
  padding: 125px 0;
  &__header {
    max-width: 576px;
    margin: 0 auto;
    text-align: center;
  }
  &__title {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(40, 35);
    line-height: 58px;
    text-transform: capitalize;
    color: #222730;
    max-width: 374px;
    margin: 0 auto;
    margin-bottom: 44px;
    &::after {
      content: url(../img/sponsors-line.svg);
      position: absolute;
      bottom: -25px;
      right: 0;
      max-width: 288px;
      width: 100%;
    }
  }
  &__sub-title {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(16, 14);
    line-height: 26px;
    color: #555555;
  }
  &__content {
    margin-top: 41px;
    display: flex;
    justify-content: center;
    gap: 32px;
    &-item {
      transition: 0.5s;
      &:hover {
        transform: scale(1.2);
      }
    }
  }
}
@include small-tablet {
  .sponsors__content {
    flex-direction: column;
    align-items: center;
  }
}
