.needs {
  padding: 130px 0;
  background: url(../img/needs-bg.jpg) no-repeat center;
  background-size: cover;
  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 716px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 60px;
  }
  &__future {
    margin-bottom: 22px;
  }
  &__title {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(54, 35);
    line-height: 74px;
    text-align: center;
    text-transform: capitalize;
    color: var(--light-color);
    margin-bottom: 40px;
  }
  &__sub-title {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(17, 15);
    line-height: 32px;
    text-align: center;
    color: rgba(223, 223, 223, 0.8);
  }
  &__card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;
    &-body {
      background: rgba(255, 255, 255, 0.15);
      border-radius: 20px;
      padding: 35px 35px 35px 80px;
    }
    &-title {
      display: flex;
      align-items: center;
      margin: 0;
      position: relative;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 400;
      @include font-adaptive(32, 25);
      line-height: 42px;
      text-transform: capitalize;
      color: var(--light-color);
      &::before {
        content: url(../img/star.svg);
        position: absolute;
        left: -45px;
      }
    }
    &-text {
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 400;
      @include font-adaptive(18, 15);
      line-height: 28px;
      color: rgba(223, 223, 223, 0.8);
    }
  }
}
@include small-tablet {
  .needs__card {
    grid-template-columns: repeat(1, 1fr);
  }
  .needs__card-body {
    padding: 30px 30px 30px 70px;
  }
}
