.hero {
  padding: 297px 0;
  position: relative;
  &__container {
    display: flex;
    align-items: center;
    row-gap: 50px;
  }
  &__content {
    max-width: 656px;
    z-index: 10;
  }
  &__title {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(60, 33);
    line-height: 86px;
    text-transform: capitalize;
    color: var(--light-color);
    margin-bottom: 45px;
    margin-top: 24px;
  }
  &__sub-title {
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(18, 15);
    line-height: 28px;
    color: var(--gray-color);
  }
  &__btn {
    display: flex;
    gap: 40px;
    &-item {
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 400;
      @include font-adaptive(18, 12);
      line-height: 28px;
      padding: 21px 34px;
      color: var(--light-color);
      transition: 0.5s;
      &:hover {
        background: #57b8ff;
      }
    }
  }
  &__img {
    margin-left: 33px;
    overflow: hidden;
    img {
      position: relative;
      z-index: 10;
    }
  }
}
.blue {
  background: #57b8ff;
  position: absolute;
  width: 47%;
  top: 0;
  right: 0;
  height: 100%;
}
@include big-desktop {
  .blue {
    width: 30%;
  }
}
@include tablet {
  .hero {
    padding: 150px 0;
  }
  .hero__container {
    flex-direction: column;
  }
  .header__right {
    gap: 10px;
  }
  .blue {
    width: 50%;
  }
}
@include mobile {
  .hero__btn {
    gap: 5px;
  }
  .hero__btn-item {
    padding: 20px 20px;
  }
}
