.era {
  padding: 130px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
  }
  &__content {
    max-width: 720px;
  }
  &__title {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(60, 39);
    line-height: 74px;
    text-transform: capitalize;
    color: var(--light-color);
    margin-top: 25px;
    margin-bottom: 40px;
  }
  &__text {
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(20, 14);
    line-height: 30px;
    color: rgba(223, 223, 223, 0.8);
    margin-bottom: 55px;
  }
  &__btn {
    cursor: pointer;
    padding: 21px 34px;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(18, 14);
    line-height: 28px;
    text-transform: capitalize;
    color: var(--light-color);
    background: #57b8ff;
    border-radius: 0px;
    transition: 0.5s;
  }
  &__vidio {
    position: relative;
    &-play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
@include tablet {
  .era__container {
    flex-direction: column;
  }
}
