.header {
  padding: 27px 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 20;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    z-index: 10;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 25px;
    &-btn {
      cursor: pointer;
      display: inline-block;
      padding: 16px 31px;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 400;
      @include font-adaptive(18, 10);
      line-height: 30px;
      color: var(--light-color);
      background: none;
      border: 1px solid #ffffff;
      transition: 0.5s;
      &:hover {
        color: var(--yellow-color);
        border: 1px solid var(--yellow-color);
      }
    }
  }
}
.nav {
  &__list {
    display: flex;
    gap: 60px;
  }
  &__link {
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--light-color);
    transition: 0.5s;
    &:hover {
      color: var(--yellow-color);
    }
  }
}
.burger {
  @include burger;
  display: none;
}
@include tablet {
  .burger {
    position: relative;
    display: block;
    z-index: 10;
    margin-left: 5px;
  }
  .nav {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background: black;
    transform: translateY(-100%);
    padding: 150px;
    transition: 0.5s;
    &__list {
      flex-direction: column;
    }
  }
  .menu--active {
    transform: translateY(0);
    transition: 0.5s;
  }
}
@include mobile {
  .header__right {
    &-btn {
      padding: 10px 10px;
    }
  }
  .logo {
    width: 100px;
  }
}
