.costomize {
  background: #2a313c;
  padding: 50px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
    flex-direction: row-reverse;
  }
  &__content {
    max-width: 560px;
  }
  &__title {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(52, 27);
    line-height: 74px;
    text-transform: capitalize;
    color: var(--light-color);
    margin-top: 24px;
    margin-bottom: 38px;
  }
  &__descr {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(18, 14);
    line-height: 30px;
    color: rgba(223, 223, 223, 0.8);
    margin-bottom: 55px;
  }
  &__list {
    display: flex;
    gap: 19px;
  }
  &__li {
    display: block;
    max-width: 175px;
    max-height: 125px;
  }
  &__item {
    cursor: pointer;
    border-radius: 20px;
    width: 100%;
    height: 100%;
    &-active {
      border: 1px solid #3f8cff;
      transition: 0.5s;
    }
  }
  &__img {
    &-content {
      overflow: hidden;
      img {
        border-radius: 20px;
        width: 100%;
      }
    }
  }
}
@include tablet {
  .costomize__container {
    flex-direction: column;
  }
}
@include mobile {
  .costomize__list {
    flex-wrap: wrap;
    justify-content: center;
  }
}
