// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Orbitron", sans-serif;
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --blue-color: #57b8ff;
  --yellow-color: #fac422;
  --gray-color: #999999;
}
